.svg {
  display: none;
}

nav {
  padding: 6px;
  position: absolute;
  z-index: 3;
  right: 20px;
  top: 40px;
  background-color: rgba(255,255,255,0.9);
  max-width: 210px;
  border-radius: 2px;
  overflow-y: scroll;
  overflow-x: hidden;
  max-height: calc(100vh - 66px);
}

canvas {
}

small {
  position: relative;
  bottom: 15px;
}

mat-slider{
  width: 100%;
}

mat-slide-toggle, label {
  margin-left: 0.25em;
}

mat-expansion-panel{
  -webkit-box-shadow: none!important;
          box-shadow: none!important;
  background: none!important;
}

#open-nav-btn{
  position: absolute;
  right: 30px;
  top: 45px;
  z-index: 3;
}

.flex{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.space-evenly{
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}
.space-between{
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.inline-flex{
  display: -ms-inline-flexbox;
  display: inline-flex;
  width: 100%;
}

.center{
  text-align: center;
}
.right{
  text-align: right;
}

.checkerz{
  background-image:
    -moz-linear-gradient(45deg, #e6e6e6 25%, transparent 25%), 
    -moz-linear-gradient(-45deg, #e6e6e6 25%, transparent 25%),
    -moz-linear-gradient(45deg, transparent 75%, #e6e6e6 75%),
    -moz-linear-gradient(-45deg, transparent 75%, #e6e6e6 75%);
  background-image:
    -webkit-gradient(linear, 0 100%, 100% 0, color-stop(.25, #e6e6e6), color-stop(.25, transparent)), 
    -webkit-gradient(linear, 0 0, 100% 100%, color-stop(.25, #e6e6e6), color-stop(.25, transparent)),
    -webkit-gradient(linear, 0 100%, 100% 0, color-stop(.75, transparent), color-stop(.75, #e6e6e6)),
    -webkit-gradient(linear, 0 0, 100% 100%, color-stop(.75, transparent), color-stop(.75, #e6e6e6));
  background-size:16px 16px;
  -webkit-background-size:16px 16px; /* override value for shitty webkit */
  
  background-position:0 0, 8px 0, 8px -8px, 0px 8px;
}

